/**
 * Tutor AI Redis Operations
 *
 * Handles storage and retrieval of:
 * - Learning profiles
 * - Tutor conversations
 * - Recommendations
 * - Analytics
 */

import { redis } from './client';
import type {
  TutorConversation,
  TutorMessage,
  LearningProfile,
  TutorRecommendation,
} from '@/types/training';

// ============ LEARNING PROFILE OPERATIONS ============

export async function getLearningProfile(
  userId: string
): Promise<LearningProfile | null> {
  const data = await redis.hgetall(`learning_profile:${userId}`);
  if (!data || !('userId' in data)) return null;

  const profile: any = { ...data };
  if (typeof profile.completedCourses === 'string') {
    profile.completedCourses = JSON.parse(profile.completedCourses || '[]');
  }
  if (typeof profile.strugglingTopics === 'string') {
    profile.strugglingTopics = JSON.parse(profile.strugglingTopics || '[]');
  }
  if (typeof profile.strongTopics === 'string') {
    profile.strongTopics = JSON.parse(profile.strongTopics || '[]');
  }
  if (typeof profile.questionsAsked === 'string') {
    profile.questionsAsked = JSON.parse(profile.questionsAsked || '[]');
  }
  if (typeof profile.recommendedNextTopics === 'string') {
    profile.recommendedNextTopics = JSON.parse(
      profile.recommendedNextTopics || '[]'
    );
  }

  return profile as LearningProfile;
}

export async function createLearningProfile(
  userId: string
): Promise<LearningProfile> {
  const now = new Date().toISOString();
  const profile: LearningProfile = {
    userId,
    completedCourses: [],
    skillLevel: 'beginner',
    preferredPace: 'normal',
    learningStyle: 'mixed',
    strugglingTopics: [],
    strongTopics: [],
    questionsAsked: [],
    recommendedNextTopics: [],
    conversationCount: 0,
    lastActiveAt: now,
  };

  await redis.hset(`learning_profile:${userId}`, {
    userId,
    completedCourses: JSON.stringify(profile.completedCourses),
    skillLevel: profile.skillLevel,
    preferredPace: profile.preferredPace,
    learningStyle: profile.learningStyle,
    strugglingTopics: JSON.stringify(profile.strugglingTopics),
    strongTopics: JSON.stringify(profile.strongTopics),
    questionsAsked: JSON.stringify(profile.questionsAsked),
    recommendedNextTopics: JSON.stringify(profile.recommendedNextTopics),
    conversationCount: 0,
    lastActiveAt: now,
  });

  return profile;
}

export async function updateLearningProfile(
  userId: string,
  updates: Partial<LearningProfile>
): Promise<void> {
  const profile = (await getLearningProfile(userId)) || (await createLearningProfile(userId));
  const updated = { ...profile, ...updates, lastActiveAt: new Date().toISOString() };

  const data: Record<string, any> = { ...updated };
  if (Array.isArray(data.completedCourses)) {
    data.completedCourses = JSON.stringify(data.completedCourses);
  }
  if (Array.isArray(data.strugglingTopics)) {
    data.strugglingTopics = JSON.stringify(data.strugglingTopics);
  }
  if (Array.isArray(data.strongTopics)) {
    data.strongTopics = JSON.stringify(data.strongTopics);
  }
  if (Array.isArray(data.questionsAsked)) {
    data.questionsAsked = JSON.stringify(data.questionsAsked);
  }
  if (Array.isArray(data.recommendedNextTopics)) {
    data.recommendedNextTopics = JSON.stringify(data.recommendedNextTopics);
  }

  await redis.hset(`learning_profile:${userId}`, data);
}

// ============ CONVERSATION OPERATIONS ============

export async function getTutorConversation(
  conversationId: string
): Promise<TutorConversation | null> {
  const data = await redis.hgetall(`tutor_conv:${conversationId}`);
  if (!data || !('id' in data)) return null;

  const conv: any = { ...data };
  if (typeof conv.messages === 'string') {
    conv.messages = JSON.parse(conv.messages || '[]');
  }
  if (typeof conv.learningProfile === 'string') {
    conv.learningProfile = JSON.parse(conv.learningProfile);
  }
  if (typeof conv.isActive === 'string') {
    conv.isActive = conv.isActive === '1' || conv.isActive === 'true';
  }

  return conv as TutorConversation;
}

export async function createTutorConversation(
  userId: string,
  courseId?: string
): Promise<TutorConversation> {
  const convId = `tutor_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`;
  const now = new Date().toISOString();

  const profile =
    (await getLearningProfile(userId)) || (await createLearningProfile(userId));

  const conversation: TutorConversation = {
    id: convId,
    userId,
    courseId,
    messages: [],
    learningProfile: profile,
    createdAt: now,
    updatedAt: now,
    isActive: true,
  };

  await redis.hset(`tutor_conv:${convId}`, {
    id: convId,
    userId,
    courseId: courseId || '',
    messages: JSON.stringify([]),
    learningProfile: JSON.stringify(profile),
    createdAt: now,
    updatedAt: now,
    isActive: 1,
  });

  await redis.sadd(`user:${userId}:tutor_convs`, convId);

  return conversation;
}

export async function addMessageToConversation(
  conversationId: string,
  message: TutorMessage
): Promise<void> {
  const conversation = await getTutorConversation(conversationId);
  if (!conversation) throw new Error('Conversation not found');

  conversation.messages.push(message);
  conversation.updatedAt = new Date().toISOString();

  await redis.hset(`tutor_conv:${conversationId}`, {
    messages: JSON.stringify(conversation.messages),
    updatedAt: conversation.updatedAt,
  });
}

export async function getUserConversations(
  userId: string,
  limit = 10
): Promise<TutorConversation[]> {
  const convIds = await redis.smembers<string[]>(`user:${userId}:tutor_convs`);
  if (!convIds.length) return [];

  const conversations = await Promise.all(
    convIds.map(id => getTutorConversation(id))
  );
  return conversations
    .filter((c): c is TutorConversation => c !== null)
    .sort((a, b) => new Date(b.updatedAt).getTime() - new Date(a.updatedAt).getTime())
    .slice(0, limit);
}

// ============ RECOMMENDATIONS OPERATIONS ============

export async function createRecommendation(
  userId: string,
  recommendation: TutorRecommendation
): Promise<void> {
  const recId = `rec_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`;

  const data: Record<string, any> = {
    ...recommendation,
    id: recId,
  };

  await redis.hset(`recommendation:${recId}`, data);
  await redis.sadd(`user:${userId}:recommendations`, recId);
  await redis.zadd(`recommendations:active`, {
    score: new Date(recommendation.suggestedAt).getTime(),
    member: recId,
  });
}

export async function getUserRecommendations(
  userId: string
): Promise<TutorRecommendation[]> {
  const recIds = await redis.smembers<string[]>(`user:${userId}:recommendations`);
  if (!recIds.length) return [];

  const recommendations = await Promise.all(
    recIds.map(async id => {
      const data = await redis.hgetall(`recommendation:${id}`);
      return data as unknown as TutorRecommendation;
    })
  );

  return recommendations.filter((r): r is TutorRecommendation => !!r);
}

// ============ ANALYTICS ============

export async function getTutorAnalytics(userId: string): Promise<{
  conversationCount: number;
  totalMessagesExchanged: number;
  averageMessagesPerConversation: number;
  topicsCovered: string[];
  lastActive: string;
}> {
  const profile = await getLearningProfile(userId);
  if (!profile)
    return {
      conversationCount: 0,
      totalMessagesExchanged: 0,
      averageMessagesPerConversation: 0,
      topicsCovered: [],
      lastActive: new Date().toISOString(),
    };

  const conversations = await getUserConversations(userId);
  const totalMessages = conversations.reduce((sum, conv) => sum + conv.messages.length, 0);
  const avgMessages =
    conversations.length > 0 ? Math.round(totalMessages / conversations.length) : 0;

  return {
    conversationCount: profile.conversationCount,
    totalMessagesExchanged: totalMessages,
    averageMessagesPerConversation: avgMessages,
    topicsCovered: [...profile.strongTopics, ...profile.strugglingTopics],
    lastActive: profile.lastActiveAt,
  };
}
