/**
 * Tutor IA Service
 *
 * Handles:
 * - System prompt building based on learning profile
 * - Context management for conversations
 * - Streaming responses from Claude
 * - Learning profile updates based on conversation
 */

import { anthropic } from '@/lib/claude';
import type {
  TutorContext,
  LearningProfile,
  TutorMessage,
} from '@/types/training';

// ============ SYSTEM PROMPTS ============

function buildSystemPrompt(context: TutorContext): string {
  const { learningProfile, currentTopic } = context;

  const strugglingAreas = learningProfile.strugglingTopics
    .slice(0, 3)
    .join(', ') || 'none identified yet';
  const strongAreas = learningProfile.strongTopics
    .slice(0, 3)
    .join(', ') || 'general concepts';

  return `You are a personalized AI tutor for SovraGov platform training. Your role is to guide partners through learning SovraGov, a digital window platform for governments.

STUDENT PROFILE:
- Skill Level: ${learningProfile.skillLevel}
- Learning Pace: ${learningProfile.preferredPace}
- Learning Style: ${learningProfile.learningStyle}
- Struggling with: ${strugglingAreas}
- Strong in: ${strongAreas}
- Conversations: ${learningProfile.conversationCount}

CURRENT CONTEXT:
- Current Topic: ${currentTopic || 'General SovraGov guidance'}
- Conversation Phase: ${context.conversationPhase}

INSTRUCTIONS:
1. **Match their pace:** Use ${learningProfile.preferredPace === 'slow' ? 'detailed explanations with examples' : learningProfile.preferredPace === 'fast' ? 'concise, direct explanations' : 'balanced explanations'}.
2. **Match their style:** Use ${learningProfile.learningStyle === 'visual' ? 'diagrams and visual descriptions' : learningProfile.learningStyle === 'interactive' ? 'interactive exercises and tasks' : learningProfile.learningStyle === 'textual' ? 'detailed text explanations' : 'a mix of text, examples, and descriptions'}.
3. **Focus on struggles:** Spend extra time on ${strugglingAreas} since they're having trouble.
4. **Build confidence:** Acknowledge what they know well.
5. **Be Socratic:** Ask guiding questions rather than just giving answers.
6. **Provide examples:** Use real SovraGov scenarios (e.g., municipal processes).
7. **Suggest next steps:** After teaching, suggest related tutorials or practice tasks.

SOVRAGOV KEY MODULES:
- Catálogos: Lists that homogenize data (simple/hierarchical)
- Trámites: Digital processes with forms and stages
- Citas: Appointment scheduling with offices/desks
- Pagos: Payment configuration with UTA/concepts
- Inspectores: On-site inspections with verification
- Emisión: Document output (PDF/blockchain)

COMMON CONFUSION POINTS:
- Catalogs vs hardcoded dropdown options
- Form sections vs steps (only steps have conditions)
- Prevention rejections vs process rejections
- Simple signatures vs electronic signatures
- UTA (indexed) vs fixed amounts

Always be encouraging, patient, and focused on their learning goals.`;
}

// ============ CONTEXT BUILDING ============

export async function buildTutorContext(
  userId: string,
  courseId: string | undefined,
  previousMessages: TutorMessage[],
  learningProfile: LearningProfile,
  currentTopic?: string
): Promise<TutorContext> {
  // Determine conversation phase based on message count
  let phase: 'greeting' | 'teaching' | 'assessment' | 'recommendation';
  if (previousMessages.length === 0) {
    phase = 'greeting';
  } else if (previousMessages.length < 5) {
    phase = 'teaching';
  } else if (previousMessages.length % 10 === 0) {
    phase = 'assessment';
  } else {
    phase = previousMessages.length > 20 ? 'recommendation' : 'teaching';
  }

  return {
    userId,
    courseId,
    previousMessages,
    learningProfile,
    currentTopic,
    conversationPhase: phase,
  };
}

// ============ STREAMING RESPONSE ============

export async function getTutorStreamingResponse(
  userMessage: string,
  context: TutorContext,
  onChunk?: (chunk: string) => void
): Promise<string> {
  if (!anthropic) {
    throw new Error('Anthropic API key is not configured');
  }

  const systemPrompt = buildSystemPrompt(context);

  // Format conversation history
  const messages = [
    ...context.previousMessages.map(msg => ({
      role: msg.role as 'user' | 'assistant',
      content: msg.content,
    })),
    { role: 'user' as const, content: userMessage },
  ];

  let fullResponse = '';

  const stream = await anthropic.messages.create({
    model: 'claude-3-5-sonnet-20241022',
    max_tokens: 1024,
    system: systemPrompt,
    messages,
    stream: true,
  });

  for await (const event of stream) {
    if (event.type === 'content_block_delta' && event.delta.type === 'text_delta') {
      const chunk = event.delta.text;
      fullResponse += chunk;
      if (onChunk) {
        onChunk(chunk);
      }
    }
  }

  return fullResponse;
}

// ============ PROFILE UPDATES ============

export async function updateProfileFromConversation(
  learningProfile: LearningProfile,
  userMessage: string,
  tutorResponse: string
): Promise<Partial<LearningProfile>> {
  // Analyze what topics were discussed
  const topics = extractTopicsMentioned(userMessage, tutorResponse);

  // Detect if user struggled
  const struggled = detectStruggle(userMessage);

  // Build updates
  const updates: Partial<LearningProfile> = {
    questionsAsked: [...(learningProfile.questionsAsked || []), userMessage].slice(-20), // Keep last 20
    conversationCount: (learningProfile.conversationCount || 0) + 1,
  };

  if (struggled) {
    const newStruggling = [...new Set([...learningProfile.strugglingTopics, ...topics])];
    updates.strugglingTopics = newStruggling;
  } else {
    const newStrong = [...new Set([...learningProfile.strongTopics, ...topics])];
    updates.strongTopics = newStrong;
  }

  return updates;
}

// ============ HELPER FUNCTIONS ============

function extractTopicsMentioned(userMessage: string, tutorResponse: string): string[] {
  const combined = `${userMessage} ${tutorResponse}`.toLowerCase();

  const sovraGovTopics = [
    'catalogs',
    'catálogos',
    'forms',
    'formulario',
    'stages',
    'etapas',
    'appointments',
    'citas',
    'payments',
    'pagos',
    'inspections',
    'inspectores',
    'emission',
    'emisión',
    'prevention',
    'prevención',
    'users',
    'usuarios',
    'roles',
    'permissions',
    'permisos',
  ];

  return sovraGovTopics.filter(topic => combined.includes(topic));
}

function detectStruggle(userMessage: string): boolean {
  const struggleIndicators = [
    'confused',
    'confundido',
    "don't understand",
    'no entiendo',
    'how do i',
    'cómo',
    'why',
    'por qué',
    'error',
    'problem',
    'problema',
    'stuck',
    'atrapado',
    'help',
    'ayuda',
  ];

  const lower = userMessage.toLowerCase();
  return struggleIndicators.some(indicator => lower.includes(indicator));
}
