import { NextRequest, NextResponse } from 'next/server';
import { requireSession } from '@/lib/auth';
import {
  getTutorConversation,
  createTutorConversation,
  addMessageToConversation,
  getLearningProfile,
  createLearningProfile,
  updateLearningProfile,
} from '@/lib/redis/tutor';
import {
  getTutorStreamingResponse,
  buildTutorContext,
  updateProfileFromConversation,
} from '@/lib/tutor/service';
import { generateId } from '@/lib/redis/operations';

export async function POST(request: NextRequest) {
  try {
    const { user } = await requireSession();

    const body = await request.json();
    const { message, conversationId, courseId, topic } = body;

    if (!message || typeof message !== 'string') {
      return NextResponse.json(
        { error: 'Message required' },
        { status: 400 }
      );
    }

    // Get or create conversation
    let conversation = conversationId
      ? await getTutorConversation(conversationId)
      : await createTutorConversation(user.id, courseId);

    if (!conversation) {
      return NextResponse.json({ error: 'Conversation not found' }, { status: 404 });
    }

    // Get or create learning profile
    let profile = await getLearningProfile(user.id);
    if (!profile) {
      profile = await createLearningProfile(user.id);
    }

    // Build context
    const context = await buildTutorContext(
      user.id,
      courseId,
      conversation.messages,
      profile,
      topic
    );

    // Stream response
    const customResponse = new ReadableStream({
      async start(controller) {
        try {
          const response = await getTutorStreamingResponse(
            message,
            context,
            (chunk: string) => {
              controller.enqueue(new TextEncoder().encode(`data: ${chunk}\n\n`));
            }
          );

          // Save messages to conversation
          const userMsg = {
            id: generateId(),
            role: 'user' as const,
            content: message,
            timestamp: new Date().toISOString(),
          };

          const assistantMsg = {
            id: generateId(),
            role: 'assistant' as const,
            content: response,
            timestamp: new Date().toISOString(),
          };

          await addMessageToConversation(conversation.id, userMsg);
          await addMessageToConversation(conversation.id, assistantMsg);

          // Update learning profile based on conversation
          const profileUpdates = await updateProfileFromConversation(
            profile,
            message,
            response
          );
          await updateLearningProfile(user.id, profileUpdates);

          // Send completion event
          controller.enqueue(
            new TextEncoder().encode(
              `data: ${JSON.stringify({ type: 'done', conversationId: conversation.id })}\n\n`
            )
          );
          controller.close();
        } catch (error) {
          console.error('Tutor stream error:', error);
          controller.enqueue(
            new TextEncoder().encode(
              `data: ${JSON.stringify({ error: String(error) })}\n\n`
            )
          );
          controller.close();
        }
      },
    });

    return new NextResponse(customResponse, {
      headers: {
        'Content-Type': 'text/event-stream',
        'Cache-Control': 'no-cache',
        Connection: 'keep-alive',
      },
    });
  } catch (error) {
    console.error('Tutor chat error:', error);
    return NextResponse.json(
      { error: 'Failed to process message' },
      { status: 500 }
    );
  }
}
